import styled from "styled-components";
import React from "react";
import {Grid} from "@material-ui/core";
import theme from "../../../theme";
import DataField from "../../../components/DataField";
import {getDisplayBalance} from "../../../utils/formatBalance";
import Button from "../../../components/Button";
import moment from "moment";
import {IuseGetMigratedNFTsData} from "../../../hooks/state/Vesting/useGetMigratedNFTsData";
import useMigrateNFT from "../../../hooks/callbacks/Vesting/useMigrateNFT";
import useHasNFTMigrated from "../../../hooks/state/Vesting/hasNFTMigrated";
import TextWrapper from "../../../components/TextWrapper";
import useIsLockValid from "../../../hooks/state/Vesting/useIsLockValid";

interface IProps {
  key: string;
  data: IuseGetMigratedNFTsData
}

const MigratingLockingDetailsCard = (props: IProps) => {
  const {
    data,
  } = props;

  const {
    amount,
    endDate,
    id,
    mahaReward,
    sclpReward,
  } = data.data

  const hasMigrated = useHasNFTMigrated(id);
  const isLockValid = useIsLockValid(data);

  const migrateNFT = useMigrateNFT(props.data);

  const handleMigrationNFT = () => {
    migrateNFT(
      () => {
        window.location.reload();
      },
      () => {
      }
    ).then(() => {
    })
  }

  // if (id.toString() === '0' || (!hasMigrated.isLoading && hasMigrated.value)) return <div />;
  // if (id.toString() === '0' || (!isLockValid.isLoading && !isLockValid.value)) return <div />;

  return (
    <Card className={'material-primary'}>
      <Grid container spacing={5}>
        <Grid item lg={4} md={4} sm={12} xs={12}>
          <NFT
            src={'https://lh3.googleusercontent.com/4gxMjQPnp8cDpdLWcjuKV_74Mu_xEBeQWi5bGTjPiNj6UnSUWSIbMy0rp2zB8mRuAEhsUZG-iNOwN7bRRRoBbGrtuZObwjuK0CDjEQ=w600'}
            width={'100%'}
          />
        </Grid>
        <Grid item lg={8} md={8} sm={12} xs={12}>
          <ContentPart>
            <InnerContentPart>
              <div>
                <div className={'m-b-8'}>
                  <DataField
                    label={'Polygon NFT ID'}
                    value={`#${id.toString()}`}
                    labelFontSize={16}
                    labelFontColor={theme.color.white}
                    valueFontSize={16}
                    valueFontColor={theme.color.white}
                    valueFontWeight={600}
                  />
                  <DataField
                    label={'NFT ID on polygon that you will migrate'}
                    labelFontColor={theme.color.transparent[100]}
                    labelFontSize={12}
                  />
                </div>
                <div className={'m-b-8'}>
                  <DataField
                    label={'Lock Amount'}
                    value={`${Number(getDisplayBalance(amount, 18, 3)).toLocaleString()} MAHA`}
                    labelFontSize={16}
                    labelFontColor={theme.color.white}
                    valueFontSize={16}
                    valueFontColor={theme.color.white}
                    valueFontWeight={600}
                  />
                  <DataField
                    label={'Amount that you locked'}
                    labelFontColor={theme.color.transparent[100]}
                    labelFontSize={12}
                  />
                </div>
                <div className={'m-b-24'}>
                  <DataField
                    label={'Lock Expires'}
                    value={moment(new Date(Number(`${endDate.toString()}000`))).format('DD-MM-YYYY')}
                    labelFontSize={16}
                    labelFontColor={theme.color.white}
                    valueFontSize={16}
                    valueFontColor={theme.color.white}
                    valueFontWeight={600}
                  />
                  <DataField
                    label={''}
                    value={
                      new Date(Number(`${endDate.toString()}000`)).getTime() > new Date().getTime()
                        ? `Expires in ${moment(new Date(Number(`${endDate.toString()}000`))).fromNow(true)}`
                        : `Expired ${moment(new Date(Number(`${endDate.toString()}000`))).fromNow(true)} ago`
                    }
                    valueFontColor={theme.color.transparent[100]}
                    valueFontSize={12}
                  />
                </div>
                <div className={'top-divider p-t-24'}>
                  <DataField
                    label={'Unclaimed Rewards'}
                    value={`${getDisplayBalance(mahaReward)} MAHA`}
                    labelFontSize={16}
                    labelFontColor={theme.color.white}
                    valueFontSize={16}
                    valueFontColor={theme.color.white}
                  />

                  <DataField
                    label={'Unclaimed MAHA and SCLP rewards you will claim when you migrate your NFT'}
                    labelFontColor={theme.color.transparent[100]}
                    labelFontSize={12}
                  />
                  <DataField
                    value={`${getDisplayBalance(sclpReward)} SCLP`}
                    valueFontSize={16}
                    valueFontColor={theme.color.white}
                  />
                </div>
              </div>
              {hasMigrated.isLoading || isLockValid.isLoading
                ? <TextWrapper text={'Fetching your NFT Status...'} align={'center'}/>
                : hasMigrated.value
                  ? <TextWrapper text={'Already Migrated'} align={'center'}/>
                  : <div className={'m-t-32'}>
                    <Button
                      text={'Migrate NFT and claim rewards'}
                      onClick={handleMigrationNFT}
                    />
                  </div>
              }
            </InnerContentPart>
          </ContentPart>
        </Grid>
      </Grid>
      <ImagePart>

      </ImagePart>
    </Card>
  )
}

export default MigratingLockingDetailsCard;

const Card = styled.div`
  border-radius: 8px;
  padding: 32px;
  margin-bottom: 24px;
  position: relative;
  @media (max-width: 600px) {
    padding: 32px 24px;
  }
`

const ImagePart = styled.div`

`

const NFT = styled.img`
  border-radius: 8px;
  max-width: 312px;
  max-height: 312px;
`

const ContentPart = styled.div`
  padding: 24px;
  border-radius: 8px;
  height: 100%;
  background: ${theme.color.transparent[500]};
`

const InnerContentPart = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
`;
