import {BigNumber, Contract} from "ethers";
import {useCallback} from "react";

import {useAddPopup} from "../../../state/application/hooks";
import {useTransactionAdder} from "../../../state/transactions/hooks";
import formatErrorMessage from "../../../utils/formatErrorMessage";
import useCore from "../../useCore";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import ABIS from "../../../protocol/deployments/abi";
import {useWallet} from "use-wallet";

export default function (nftID: BigNumber, gaugeAddress: string) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();
  const {account} = useWallet();

  return useCallback(
    async (onSuccess: () => void, onFailure: () => void): Promise<void> => {
      const contract = new Contract(gaugeAddress, ABIS.BaseGaugeV2UniV3, core._signer);

      try {
        const response = await contract.claimReward(nftID, account);

        addTransaction(response, {
          summary: `Claim rewards from ${nftID.toString()} staking`,
        });

        const tx = await response.wait();

        if (tx?.status === 1) onSuccess();
        if (tx?.status !== 1) onFailure();

      } catch (e: any) {
        console.log('useV3Claim error', e);
        onFailure();
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e.stack,
          },
        });
      }
    },
    [gaugeAddress, core.provider, core._tokens, chainId, account, addTransaction, addPopup],
  );
}
