import React, { useState } from "react";
import DataField from "../../../components/DataField";
import { getDisplayBalance } from "../../../utils/formatBalance";
import TextButton from "../../../components/TextButton";
import TextWrapper from "../../../components/TextWrapper";
import theme from "../../../theme";
import { BigNumber } from "ethers";
import useClaimFeesRewards from "../../../hooks/callbacks/Fees/useClaimFeesRewards";
import useGetFeesRewards from "../../../hooks/state/Fees/useGetFeesRewards";
import Loader from "react-spinners/BeatLoader";

interface IProps {
  token: string;
  nftId: BigNumber;
}

const TokenRewards = (props: IProps) => {
  const { token, nftId } = props;
  const [claiming, setClaiming] = useState<boolean>(false);

  const claim = useClaimFeesRewards(token, nftId);
  const reward = useGetFeesRewards(token, nftId);

  const handleClaim = () => {
    setClaiming(true);
    claim(
      () => {
        setClaiming(false);
        window.location.reload();
      },
      () => {
        setClaiming(false);
      }
    )
  }

  return (
    <div className={'m-b-16'}>
      <DataField
        label={token}
        isValueLoading={false}
        value={`${Number(getDisplayBalance(reward.value, 18, 18)).toLocaleString('en-US', {
          minimumFractionDigits: 2,
          maximumFractionDigits: 6
        })} ${token}`}
        labelFontSize={16}
        valueFontSize={16}
        valueFontColor={"#FFF"}
        className={'m-b-4'}
      />
      <div className={'single-line-center-end'}>
        {!reward.isLoading && !claiming
          ? <TextButton
            onClick={() => {
              handleClaim()
            }}
            text={'Claim'}
            tracking_id={'claim_protocol_fees'}
            tracking_params={{
              collateral: token,
              other: { nftId: nftId.toString() }
            }}
            disabled={reward.value.eq(0)}
          />
          : claiming
            ? <TextWrapper text={'Claiming'} Fcolor={theme.color.transparent[100]} />
            : <Loader color={'#ffffff'} loading={true} size={2} margin={2} />
        }
      </div>
    </div>
  )
}

export default TokenRewards;
