import TextWrapper, {TextWrapperProps} from "../TextWrapper";
import theme from "../../theme";
import React from "react";
import {tracking_params} from "../Button/Button";
import {Mixpanel} from "../../analytics/Mixpanel";
import config from "../../config";

interface IProps {
  onClick: () => void;
  tracking_id?: string;
  tracking_params?: tracking_params;
  disabled?: boolean;
}

const TextButton = (props: IProps & TextWrapperProps) => {
  const {onClick, tracking_id, tracking_params = {}, disabled = false} = props;

  const trackMixpanel = () => {
    const params = {
      networkName: config.networkDisplayName || "",
      ...tracking_params,
    };
    if (tracking_id) {
      Mixpanel.track(`buttonClick:${tracking_id.toLowerCase()}`, params)
    }
  }

  return (
    <div
      className={disabled ? 'not-allowed' : 'pointer'}
      onClick={() => {
        if (disabled) return;
        if (tracking_id) trackMixpanel();
        if (onClick) onClick();
      }}
    >
      <TextWrapper
        Fcolor={disabled ? theme.color.transparent[100] : theme.color.primary[300]}
        {...props}
      />
    </div>
  )
}

export default TextButton;
