import {BigNumber} from "ethers";
import {useCallback} from "react";
import {useTransactionAdder} from "../../../state/transactions/hooks";

import useCore from "../../useCore";
import {useAddPopup} from "../../../state/application/hooks";
import formatErrorMessage from "../../../utils/formatErrorMessage";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import {IuseGetMigratedNFTsData} from "../../state/Vesting/useGetMigratedNFTsData";

const useMigrateNFT = (data: IuseGetMigratedNFTsData) => {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();

  return useCallback(
    async (onSuccess: () => void, onFailure: () => void): Promise<void> => {
      try {
        const contract = core.getMigrationContract(chainId);
        /* console.log(
           JSON.stringify(
             [
               data.data.amount.toString(),
               data.data.endDate,
               data.data.id,
               data.data.owner,
               data.data.mahaReward.toString(),
               data.data.sclpReward.toString(),
               data.proof.toString(),
             ],
             null,
             2
           )
         );*/
        console.log(contract.address);
        const response = await contract.migrateLock(
          data.data.amount.toString(),
          data.data.endDate,
          data.data.id,
          data.data.owner,
          data.data.mahaReward.toString(),
          data.data.sclpReward.toString(),
          data.proof
        );

        addTransaction(response, {
          summary: `Migrate Polygon NFT #${data.data.id}`,
        });

        const tx = await response.wait();

        if (tx?.status === 1) onSuccess();
        if (tx?.status !== 1) onFailure();
      } catch (e: any) {
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
        onFailure();
      }
    },
    [core, chainId, data, addTransaction, addPopup]
  );
};

export default useMigrateNFT;
