import { useWallet } from "use-wallet";
import { useCallback, useEffect, useState } from "react";
import {
  LOADING_DEFAULT_BOOLEAN_STATE,
  NON_LOADING_DEFAULT_BOOLEAN_STATE,
} from "../../../utils/constants";
import { BasicBooleanState } from "../../../utils/interface";
import useCore from "../../useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";

const useHasNFTMigrated = (nftId: string) => {
  const [value, setValue] = useState<BasicBooleanState>(
    LOADING_DEFAULT_BOOLEAN_STATE
  );

  const core = useCore();
  const { account } = useWallet();
  const chainId = useGetActiveChainId();
  const contract = core.getMigrationContract(chainId);

  const fetchData = useCallback(async () => {
    if (!account && contract === undefined) {
      setValue(NON_LOADING_DEFAULT_BOOLEAN_STATE);
      return;
    }

    const value = await contract.isTokenIdMigrated(nftId);

    setValue({
      isLoading: false,
      value: value,
    });
  }, [account, contract, nftId]);

  useEffect(() => {
    if (core) {
      fetchData().catch((err) => {
        setValue(NON_LOADING_DEFAULT_BOOLEAN_STATE);
        console.error(`Failed to fetch voting status: ${err.stack}`);
      });
    }
  }, [setValue, core, account, fetchData]);

  return value;
};

export default useHasNFTMigrated;
